<template>
	<div class="detail">
		<div class="detail-head">
			<div class="detail-head-name">
				<div class="detail-icon-wrapper">
					<svg width="14" height="13">
						<use xlink:href="#tableicon"></use>
					</svg>
				</div>
				<template v-if="name != 'tableAddElement'">
					<div class="detail-name-wrapper">
						<div class="detail-head-label">{{$t('pages.table.edit_element')}}</div>
						<div class="detail-head-descr">{{ tableCode }}</div>
					</div>
					<div class="detail-head__buttons">
						<button @click="cancel" class="el-gbtn">{{$t('cancel')}}</button>
						<button @click="remove" class="el-gbtn">{{$t('remove')}}</button>
						<button @click="saveElement" class="el-btn">{{$t('save')}}</button>
					</div>
				</template>
				<template v-else>
					<div class="detail-name-wrapper">
						<div class="detail-head-label">{{$t('pages.table.new_element')}}</div>
						<div class="detail-head-descr">{{ tableCode }}</div>
					</div>
					<div class="detail-head__buttons">
						<button @click="cancel" class="el-gbtn">{{$t('cancel')}}</button>
						<button @click="createElement" class="el-btn">{{$t('create')}}</button>
					</div>
				</template>
			</div>
		</div>
		<div class="detail-feilds">
			<div class="detail-feild" v-for="(column,columnCode) in selectedElement">
				<div class="detail-field__name-wrap">
					<img class="detail-field__icon-image" :src="require(`@/assets${columnEmSettings(columnCode).type_info.iconPath}`)">
					<div class="detail-field-name">
						<span>{{ getColumnName(columnCode) }}</span>
						<small>{{ columnCode }}</small>
					</div>
				</div>
				<div class="detail-field-box">
					<MainField
						mode="edit"
						view="detail"
						:fieldName="column.fieldName"
						:params="{
							value     : column.value,
							settings  : $store.getters.getColumnSettings(tableCode, columnCode, selectedElement)
						}"
						@onChange="changeFieldValue"
					/>
				</div>
			</div>
		</div>
	</div>
</template>
<script>
	import MainField from '@/components/fields/MainField.vue';
	import qs from 'qs';
	export default
	{
		props: ['tableCode', 'name', 'id'],
		components: {MainField},
		data()
		{
			return {
				columns:{},
				selectedElement:{}
			}
		},
		mounted()
		{
			let requestParams = {
				select : {},
				where  : [],
				order  : [],
			};
			requestParams.select.from = this.tableCode;
			let primaryKeyCode        = this.$store.getters.getPrimaryKeyCode(this.tableCode);
			this.columns              = this.$store.getters.getColumns(this.tableCode);
			if(this.name != 'tableAddElement')
			{
				requestParams.select.where = {
					operation:'and',
					fields:[
						{
							code      : primaryKeyCode,
							operation : 'IS',
							value     : this.id
						}
					]
				}
				this.$store.dispatch('selectElement',requestParams).then(()=>
				{
					this.selectedElement = this.$store.state.tables.selectedElement;
				});
			}
			else
			{
				for(let columnCode in this.columns)
				{
					this.$set(this.selectedElement, columnCode,{
						value     :'',
						fieldName :this.columns[columnCode].em.type_info.code
					});
				}
			}

		},
		methods:
		{
			/**
			 * Триггер изменения значения филда
			 * Переносим значение в наш стейт
			 * @fieldValue {
			 *     value,
			 *     settings(основной формат)
			 * }
			 */
			changeFieldValue(fieldValue)
			{
				this.selectedElement[fieldValue.settings.fieldCode].value = fieldValue.value;
			},

			/**
			 * Return em object of column
			 */
			columnEmSettings(columnCode)
			{
				if(typeof this.columns[columnCode] != 'undefined')
					return this.columns[columnCode].em;
				throw new Error(`No column with code ${columnCode}`);
			},

			/**
			 * Returns column name or code
			 */
			getColumnName(columnCode)
			{
				if(!this.columnEmSettings(columnCode).name)
					return columnCode;
				return this.columnEmSettings(columnCode).name
			},

			/**
			 * Сохранение элемента
			 */
			async saveElement()
			{
				this.$store.dispatch('saveSelectedElement',{
					selectedElement : this.selectedElement,
					tableCode       : this.tableCode
				}).then(()=>
				{
					this.ElMessage(this.$t('elMessages.element_saved'));
				});
			},

			/**
			 * Создание элемента переход на страницу редактирования
			 */
			async createElement()
			{
				let primaryKeyCode = this.$store.getters.getPrimaryKeyCode(this.tableCode);
				let setColumns  = [];
				let setValues  = [];
				for(let fieldCode in this.selectedElement)
				{
					if(primaryKeyCode == fieldCode) continue;
					setColumns.push(fieldCode);
					setValues.push(this.selectedElement[fieldCode].value);
				}

				var data = qs.stringify({
					insert:
					{
						table   :this.tableCode,
						columns :setColumns,
						values  :setValues
					}
				});
				let result = await this.$axios.post('/el/insert/',data);
				if(result.data.success == true)
				{
					this.$emit('openCreatedDetail', {tableCode:this.tableCode, id:result.data.lastid })
					this.ElMessage(this.$t('elMessages.element_created'));
				}
				else
					this.ElMessage.error(this.$t('elMessages.cant_create_element'));
			},

			/**
			 * Отмена редактирования
			 */
			cancel()
			{
				this.$emit('cancel');
			},

			/**
			 * Удаление элемента
			 */
			async remove()
			{
				let primaryKeyCode = this.$store.getters.getPrimaryKeyCode(this.tableCode);
				await this.$store.dispatch('removeRecord', {
					delete:
					{
						table: this.tableCode,
						where:
						{
							operation:'and',
							fields:[
								{
									code      : primaryKeyCode,
									operation : 'IS',
									value     : this.selectedElement[primaryKeyCode].value
								}
							]
						}
					}
				}).then(()=>
				{
					this.cancel();
					this.ElMessage(this.$t('elMessages.element_removed'));
				});
			}
		}
	}
</script>
